// ---------------------------------- \\
//              COLORS                \\
// ---------------------------------- \\

$gray-100: #fafafa;
$gray-200: #c4c9cb;
$gray-300: #9aa2a9;
$gray-400: #6F7281;
$gray-500: #53555c;
$gray-600: #363639;
$gray-700: #23272e;

$navy-100: #E4E9ED;
$navy-400: #1451A5;
$navy-700: #082f56;

$orange-100: #FEDDC3;
$orange-400: #D28A25;
$orange-700: #B34B18;

$purple-100: #E1DDF4;
$purple-400: #6B52C7;
$purple-700: #35247C;

$blue-100: #C8E4FE;
$blue-400: #1861C8;
$blue-700: #0F3F81;

$green-100: #d2ece3;
$green-400: #1a8a60;
$green-700: #0D5038;

$yellow-100: #FEE9B4;
$yellow-400: #D28A25;
$yellow-700: #865C18;

$red-100: #f8d8d9;
$red-400: #da3e42;
$red-700: #881315;

$color-primary: $purple-400;
$color-primary-light: $purple-100;
$color-primary-dark: $purple-700;
$color-secondary: $purple-700;
$color-secondary-light: $purple-100;
$color-secondary-dark: $purple-700;

$color-danger: $red-400;
$color-danger-light: $red-100;
$color-danger-dark: $red-700;
$color-warning: $yellow-400;
$color-warning-light: $yellow-100;
$color-warning-dark: $yellow-700;
$color-success: $green-400;
$color-success-light: $green-100;
$color-success-dark: $green-700;
$color-info: $blue-400;
$color-info-light: $blue-100;
$color-info-dark: $blue-700;


// ---------------------------------- \\
//             TYPOGRAPHY             \\
// ---------------------------------- \\

$title-color: $gray-600;

$text-color: $gray-400;
$text-color-light: $gray-300;
$text-color-dark: $gray-500;

$link-color: $color-info;
$link-color-light: $color-info-light;

$base-font-size: 16px;

$font-family-title: "Gilroy","Inter",system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
$font-family-text: "Inter var","Inter",system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
$font-family-mono: "Roboto Mono", monospace;


// ---------------------------------- \\
//               SPACING              \\
// ---------------------------------- \\

$padding-base: 1rem;

$padding-xs: calc($padding-base / 4);
$padding-sm: calc($padding-base / 2);
$padding-md: $padding-base;
$padding-lg: calc($padding-base * 1.5);
$padding-xl: calc($padding-base * 2);
$padding-xxl: calc($padding-base * 2.5);


// ---------------------------------- \\
//               BORDER               \\
// ---------------------------------- \\

$border-width: 2px;
$border-style: solid;
$border-color: $gray-200;
$border: $border-width $border-style $border-color;
$border-radius: 0.25rem;


// ---------------------------------- \\
//              ELEVATION             \\
// ---------------------------------- \\

$elevation-100: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1),
                0 0.125rem 0.25rem rgba(0, 0, 0, 0.1);
$elevation-200: 0 1rem 4rem -2rem rgba(0, 0, 0, 0.5), 
                0 0.5rem 0.5rem rgba(0, 0, 0, 0.1),
                0 0.25rem 0.25rem rgba(0, 0, 0, 0.1);
$elevation-300: 0 2rem 10rem -5rem rgba(0, 0, 0, 0.8), 
                0 1rem 4rem -2rem rgba(0, 0, 0, 0.5), 
                0 0.5rem 0.5rem rgba(0, 0, 0, 0.1),
                0 0.5rem 0.5rem rgba(0, 0, 0, 0.1),
                0 0.25rem 0.25rem rgba(0, 0, 0, 0.1);

$box-shadow: $elevation-100;
$box-shadow-dark: $elevation-200;


// ---------------------------------- \\
//          TIMING FUNCTIONS          \\
// ---------------------------------- \\

$ease-sine: cubic-bezier(0.37, 0, 0.63, 1);
$ease-quad: cubic-bezier(0.45, 0, 0.55, 1);
$ease-cubic: cubic-bezier(0.65, 0, 0.35, 1);
$ease-quart: cubic-bezier(0.76, 0, 0.24, 1);
$ease-quint: cubic-bezier(0.83, 0, 0.17, 1);
$ease-expo: cubic-bezier(0.87, 0, 0.13, 1);
$ease-circ: cubic-bezier(0.85, 0, 0.15, 1);
$ease-bounce: cubic-bezier(0.68, -0.6, 0.32, 1.6);

$transition-duration: 150ms;
$transition-timing-function: $ease-cubic;


// ---------------------------------- \\
//        INPUTS AND CONTROLS         \\
// ---------------------------------- \\

$input-height: 3rem;
$input-icon-width: $input-height;
$input-label-height: ($input-height * 0.5);
$input-help-text-height: $input-label-height;
$input-abbrev-width: 5rem;
